/*** imports ***/

@import url('https://fonts.googleapis.com/css2?family=Rubik:wght@500&display=swap');

/*** end imports ***/

/*** theme ***/
:root {
    --orange-main: #f35001;
    --orange-light: #feaf17;
    --yellow-main: #fee71a;
    --green-main: #5c7944;
    --green-light: #abcb42;
    --primary-bg: #bee5b0;
    --secondary-bg: #ffffa5;
}
/*** end theme ***/

/*** layout ***/
:root {
    --navbar-height: 50px;
}

body {
    margin: 0;
    padding: 0;
    min-width: 320px;
    outline: red solid;
    background-color: var(--green-light);
}

div#root {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
}

footer {
    flex: none;
}

main {
    padding-inline: 10vw;
    padding-top: 10px;
    padding-bottom: 5vh;
    background: var(--primary-bg);
    margin-top: var(--navbar-height);
    flex: auto;

    overflow-x: auto;
}

@media only screen and (max-width: 800px) {
    main {
        padding-inline: 10px;
    }
}

footer {
    text-align: center;
    padding: 10px;
    background: var(--green-main);
    color: var(--primary-bg);
    flex: none;
}

footer a,
footer a:visited {
    color: var(--green-light);
}

footer a:hover,
footer a:visited:hover {
    color: var(--yellow-main);
}

footer a:active,
footer a:visited:active {
    color: var(--green-light);
}

footer {
    bottom: 0;
}

/*** end layout ***/

/*** text and links ***/

body {
    font-family: 'Rubik', sans-serif;
    font-size: 18px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    color: var(--orange-main);
    text-align: center;
}

h1 {
    font-size: 3rem;
}

a,
a:visited {
    color: var(--green-main);
    text-decoration: none;
}

a:hover,
a:visited:hover {
    color: var(--orange-main);
}

a:active,
a:visited:active {
    color: var(--yellow-main);
}

/*** end text and links ***/

/*** scrollbar ***/

body::-webkit-scrollbar-track {
    margin-top: var(--navbar-height);
}

::-webkit-scrollbar {
    width: 10px;
    height: 10px;
}

::-webkit-scrollbar-track {
    background: var(--primary-bg);
    width: 10px;
}

::-webkit-scrollbar-thumb {
    background: var(--orange-main);
    outline: red;
    border-radius: 20px;
}

::-webkit-scrollbar-thumb:hover {
    filter: brightness(50%);
}

/*** end scrollbar ***/

dialog {
    background-color: var(--primary-bg);
    border-color: var(--orange-main);
    border-radius: 10px;
}

dialog::backdrop {
    filter: brightness(80%);
    background-color: rgba(92, 121, 68, 0.8);
}

